import { FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
import React from "react";

const SelectPay24 = ({ item, classes, handleChange }) => {
  return (
    <FormControl className={classes.formControl}>
      <InputLabel>{item["@label"]}</InputLabel>
      <Select
        name={item["@name_code"]}
        variant="outlined"
        required
        label={item["@label"]}
        onChange={handleChange}
      >
        {item.data.map((el, idx) => (
          <MenuItem key={idx} aria-label="None" value={Object.values(el)[0]}>
            {Object.values(el)[1]}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default SelectPay24;
