import { makeStyles, Typography, Box, Button } from "@material-ui/core";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getPaymentDetails } from "../../../../redux/reducers/binancePay";

const ThirdStep = ({ t, handleReset }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { check, payment } = useSelector(({ Pay24Reducer }) => Pay24Reducer);
  const { qrCode, payment_details } = useSelector(
    ({ binancePay }) => binancePay
  );

  useEffect(() => {
    setTimeout(() => {
      if (!payment_details?.paid) dispatch(getPaymentDetails(qrCode?.guid));
    }, 10000);
  }, [payment_details]);

  return (
    <Box
      sx={{
        backgroundColor: "#fff",
        padding: 32,
        borderRadius: 16,
        boxShadow: "0px 1px 0px #E2E8F0",
      }}
    >
      {qrCode.status === "SUCCESS" ? (
        <Box
          display="flex"
          justifyContent="center"
          flexDirection="column"
          color="#000"
          alignItems="center"
        >
          {payment_details?.error_code_from_pay24 !== "0" ? (
            <>
              <svg
                width="160"
                height="160"
                viewBox="0 0 160 160"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M80 13.3333C43.2667 13.3333 13.3334 43.2667 13.3334 80C13.3334 116.733 43.2667 146.667 80 146.667C116.733 146.667 146.667 116.733 146.667 80C146.667 43.2667 116.733 13.3333 80 13.3333ZM75 53.3333C75 50.6 77.2667 48.3333 80 48.3333C82.7334 48.3333 85 50.6 85 53.3333V86.6667C85 89.4 82.7334 91.6667 80 91.6667C77.2667 91.6667 75 89.4 75 86.6667V53.3333ZM86.1334 109.2C85.8 110.067 85.3334 110.733 84.7334 111.4C84.0667 112 83.3334 112.467 82.5334 112.8C81.7334 113.133 80.8667 113.333 80 113.333C79.1334 113.333 78.2667 113.133 77.4667 112.8C76.6667 112.467 75.9334 112 75.2667 111.4C74.6667 110.733 74.2 110.067 73.8667 109.2C73.5334 108.4 73.3334 107.533 73.3334 106.667C73.3334 105.8 73.5334 104.933 73.8667 104.133C74.2 103.333 74.6667 102.6 75.2667 101.933C75.9334 101.333 76.6667 100.867 77.4667 100.533C79.0667 99.8667 80.9334 99.8667 82.5334 100.533C83.3334 100.867 84.0667 101.333 84.7334 101.933C85.3334 102.6 85.8 103.333 86.1334 104.133C86.4667 104.933 86.6667 105.8 86.6667 106.667C86.6667 107.533 86.4667 108.4 86.1334 109.2Z"
                  fill="#EF4444"
                />
              </svg>
              <Typography variant="h2" className={classes.error}>
                Что-то пошло не так
              </Typography>
              <Button
                onClick={handleReset}
                variant="contained"
                type="button"
                color="secondary"
                size="large"
                className={classes.button}
              >
                {t("tryAgain")}
              </Button>
            </>
          ) : payment_details?.paid &&
            payment_details?.error_code_from_pay24 === "0" ? (
            <>
              <Typography variant="h4" className={classes.subtitle}>
                {t("paymentWasSuccessful")}{" "}
              </Typography>
              <Typography variant="h3" className={classes.title}>
                {t("paymentCheck")}
              </Typography>
              <ul className={classes.ul}>
                <Typography variant="h5" component="li">
                  {t("AmountToBeDebited")}
                  <div></div>
                  <span>{check?.data?.amount_usdt} USDT</span>
                </Typography>
                <Typography variant="h5" component="li">
                  {t("atRate")}
                  <div></div>
                  <span>{check?.data?.currency} KGS</span>
                </Typography>
                <Typography variant="h5" component="li">
                  {t("ReceiptNumber")}
                  <div></div>
                  <span>{check?.data?.guid}</span>
                </Typography>
                <Typography variant="h5" component="li">
                  {t("AmountReceivable")}
                  <div></div>
                  <span>{check?.data?.amount_kgs} KGS</span>
                </Typography>
                <Typography variant="h5" component="li">
                  {t("systemCommission")}
                  <div></div>
                  <span>{check?.data?.pay24_commission} KGS</span>
                </Typography>
                <Typography variant="h5" component="li">
                  {t("OurCommission")}
                  <div></div>
                  <span>{check?.data?.netex_commission} KGS</span>
                </Typography>
              </ul>
              <Button
                onClick={handleReset}
                variant="contained"
                type="button"
                color="primary"
                size="large"
                className={classes.button}
              >
                {t("reset")}
              </Button>
            </>
          ) : (
            <>
              <Typography
                style={{
                  textAlign: "center",
                }}
              >
                {t("binancePayTitle")} "{t("SliderButton2")}"
              </Typography>
              <img
                src={qrCode.data.qrcodeLink}
                width="300"
                height="300"
                style={{ display: "block", margin: "0 auto" }}
                alt=""
              />

              <Typography
                style={{
                  marginBottom: 16,
                }}
              ></Typography>
              <Button
                size="large"
                variant="contained"
                color="primary"
                href={qrCode.data.universalUrl}
                target="_blank"
              >
                {t("SliderButton2")}
              </Button>
            </>
          )}
        </Box>
      ) : (
        <>
          <Typography variant="h4" className={classes.subtitle}>
            {payment?.data?.result}
          </Typography>
          <Typography variant="h3" className={classes.title}>
            {t("paymentCheck")}
          </Typography>
          <ul className={classes.ul}>
            <Typography variant="h5" component="li">
              {t("AmountToBeDebited")}
              <div></div>
              <span>{check?.data?.amount_usdt} USDT</span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("atRate")}
              <div></div>
              <span>{check?.data?.currency} KGS</span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("ReceiptNumber")}
              <div></div>
              <span>{check?.data?.guid}</span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("AmountReceivable")}
              <div></div>
              <span>{check?.data?.amount_kgs} KGS</span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("systemCommission")}
              <div></div>
              <span>{check?.data?.pay24_commission} KGS</span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("OurCommission")}
              <div></div>
              <span>{check?.data?.netex_commission} KGS</span>
            </Typography>
          </ul>
          <Button
            onClick={handleReset}
            variant="contained"
            type="button"
            color="primary"
            size="large"
            className={classes.button}
          >
            {t("reset")}
          </Button>
        </>
      )}
    </Box>
  );
};

export default ThirdStep;

const useStyles = makeStyles((theme) => ({
  title: {
    color: "#000",
    textAlign: "center",
    lineHeight: "1",
    marginBottom: 24,
  },

  subtitle: {
    textAlign: "center",
    marginBottom: 32,
    lineHeight: "1",
    color: "#00bb29",
    marginTop: 10,
    fontWeight: 300,
  },

  button: {
    width: "100%",
    height: 56,
  },
  error: {
    fontWeight: 600,
    marginTop: 16,
    marginBottom: 16,
  },

  ul: {
    width: "100%",
    "& li": {
      color: "#000",
      fontWeight: 300,
      marginBottom: 16,
      display: "flex",
      justifyContent: "space-between",
      whiteSpace: "nowrap",
      alignItems: "baseline",

      "& div": {
        width: "100%",
        height: "fit-content",
        borderBottom: "2px dotted #B8C0CC",
        margin: "0 3px",
      },

      "& span": {
        fontWeight: 500,
      },
    },
  },
}));
