/* eslint-disable eqeqeq */
import React, { useLayoutEffect, useRef } from "react";
import { Box, Button, makeStyles, Typography } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { payPayment } from "../../../../redux/reducers/pay24";
import { getQrCode } from "../../../../redux/reducers/binancePay";
import logo from "../../../../assets/images/binance-pay/binance-pay.png";

const SecondStep = ({ t, setLoading, loading, handleNext, main }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { check, payment } = useSelector(({ Pay24Reducer }) => Pay24Reducer);
  const { qrCode } = useSelector(({ binancePay }) => binancePay);

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (payment?.status == 200) {
      handleNext();
      setLoading(false);
    } else {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [payment]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (qrCode?.status == "SUCCESS") {
      handleNext();
      setLoading(false);
    } else {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [qrCode]);

  <to
    service="1781"
    currency="417"
    commission="5"
    account="8600490495718736"
    amount="5"
    comment=""
    identification_data="Ya"
    fio_uzcard="Ya"
    fio="Ya"
    payer_dateto="Ya"
    payer_issuer="Ya"
    payer_passport_num="Ya"
    payer_inn="Ya"
    to_fio="Ya"
  />;

  return (
    <Box
      sx={{
        backgroundColor: "#fff",
        padding: 32,
        borderRadius: 16,
        boxShadow: "0px 1px 0px #E2E8F0",
      }}
    >
      <Typography variant="h3" className={classes.title}>
        {t("paymentСonfirmation")}
      </Typography>
      <Typography variant="h4" className={classes.subtitle}>
        {check?.data?.result}
      </Typography>
      <ul className={classes.ul}>
        <Typography variant="h5" component="li">
          {t("AmountToBeDebited")}
          <div></div>
          <span>{check?.data?.amount_usdt} USDT</span>
        </Typography>
        <Typography variant="h5" component="li">
          {t("AmountReceivable")}
          <div></div>
          <span>{check?.data?.amount_kgs} KGS</span>
        </Typography>
        <Typography variant="h5" component="li">
          {t("systemCommission")}
          <div></div>
          <span>{check?.data?.pay24_commission} KGS</span>
        </Typography>
        <Typography variant="h5" component="li">
          {t("OurCommission")}
          <div></div>
          <span>{check?.data?.netex_commission} KGS</span>
        </Typography>
        {!main && (
          <Button
            variant="contained"
            color="primary"
            disabled={loading}
            className={classes.button}
            size="large"
            onClick={() => {
              setLoading(true);
              dispatch(payPayment(check.data.id));
            }}
          >
            {t("confirm")}
          </Button>
        )}
        <Button
          style={{ marginTop: 16 }}
          variant="contained"
          color="secondary"
          className={classes.button}
          size="large"
          onClick={() => {
            dispatch(getQrCode(check.data.id));

            setLoading(true);
          }}
        >
          <img src={logo} width="100" alt="" />
        </Button>
      </ul>
    </Box>
  );
};

export default SecondStep;

const useStyles = makeStyles((theme) => ({
  title: {
    color: "#000",
    textAlign: "center",
    lineHeight: "1",
  },

  subtitle: {
    textAlign: "center",
    marginBottom: 50,
    lineHeight: "1",
    color: "#00bb29",
    marginTop: 10,
    fontWeight: 300,
  },

  button: {
    width: "100%",
    height: 56,
  },

  ul: {
    width: "100%",
    "& li": {
      color: "#000",
      fontWeight: 300,
      marginBottom: 16,
      display: "flex",
      justifyContent: "space-between",
      whiteSpace: "nowrap",
      alignItems: "baseline",

      "& div": {
        width: "100%",
        height: "fit-content",
        borderBottom: "2px dotted #B8C0CC",
        margin: "0 3px",
      },

      "& span": {
        fontWeight: 500,
        maxWidth: "50%",
      },
    },
  },
}));
